.form {
    border-radius: 16px;
    background-color: springgreen;
    width: 50%;
    display: flex;
    flex-direction: column;
    padding: 30px;
    justify-content: center;
    align-items: center;
}

.input {
    outline: none;
    border-radius: 12px;
    border: 2px solid red;
    height: 30px;
    margin-bottom: 10px;
}

.button {
    margin-top: 20px;
    background-color: snow;
    outline: none;
    border: none;
    transition: .5s;
    border-radius: 12px;
    height: 30px;
    margin-bottom: 10px;
    -webkit-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}

.button:hover {
    -webkit-box-shadow: -3px 23px 8px 14px rgba(34, 60, 80, 0.14);
    -moz-box-shadow: -3px 23px 8px 14px rgba(34, 60, 80, 0.14);
    box-shadow: -3px 23px 8px 14px rgba(34, 60, 80, 0.14);
}