.todo {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    width: 60%;
    padding: 2px 10px;
    border: 2px solid red;
    border-radius: 16px;
    margin-bottom: 10px;
    transition: .7s;
}

.todo:hover {

}

.todoTitle {
    font-size: 20px;
    font-weight: 900;
}

.author {
    font-size: 14px;
    font-weight: 400;
}

.actions {
    display: flex;
    justify-content: space-between;
}

.done {
    font-size: 20px;
    font-weight: 900;
    text-decoration: line-through;
}

.img {
    width: 90%;
    margin-bottom: 20px;
    border-radius: 4px;
    /*height: 200px;*/
}